<style lang="less">
.main {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  display: flex;
  // 横着摆放
  h2{
    margin: 0;
  }
}
  #nav{
    width: 180px;
    border-right: 1px solid;
     padding: 10px;
  flex-grow: 0;
  flex-shrink: 0;
  }

.global-stat{
  text-align: right;
}
  a {
    display: block;
    font-weight: bold;
    color: #2c3e50;
    &.router-link-exact-active {
      color: #42b983;
    }
  }

</style>

<template>
  <div id="app">
<div class="main">
    <div id="nav">
      <select v-model="selectedServer" @change="switchServer">
        <option v-for="(server,idx) of servers" :key="server" :value="idx">{{ server.name || server.host +':'+server.port}}</option>
      </select>
      {{ connectionStatus }}
      <router-link to="/">正在下载({{ +globalStat.numWaiting + +globalStat.numActive }})</router-link> 
      <router-link to="/completed">已完成({{ globalStat.numStopped }})</router-link>
      <router-link to="/new">新建下载</router-link>
      <router-link to="/settings">设置</router-link>
      <routrr-link to="/servers">服务器</routrr-link>
    </div> 
    <router-view :aria2="aria2" @servers-changed ="servers = $event" />
  </div>
<div class="global-stat">全局上传速度:{{ globalStat.uploadSpeed }} 全局下载速度:{{ globalStat.downloadSpeed }}</div>
</div>
</template>
<script>
import Aria2Client from './aria2-client.js'
export default {
  data(){
    var servers = JSON.parse(localStorage.getItem('aria2Servers')) || []
    return{
      globalStat:{},
      severs:servers,
      selectedServer:null,
      aria2:null
    }
  },
  methods:{
   async switchServer(){
     this.aria2?.close()
     this.globalStat = {}
     //关闭当前服务器,清空状态
     this.connectionStatus = '连接中...'
      var aria2 = new Aria2Client(this.selectedServer)
    this.aria2 = aria2
    try{
      await aria2.ready()
      this.connectionStatus = '已连接'
    }catch(e){
      this.connectionStatus = '连接失败'
    }
    },
    startIntervalUpdate(){
          this.intervalId = setInterval(async()=>{
           try{
             this.globalStat = await this.aria2.getGlobalStat()
           }catch(e){
             if(e == 'WS_CONNECT_ERROR'){
               clearInterval(this.intervalId)
             }else{
               throw e
             }
           }
    },1000)
    }
  },
  created(){
    this.selectedServer = this.servers[0]
    this.switchServer()
  },
 async mounted(){
   // 子组件先Mounted，父组件后mounted
   try{
     this.globalStat = await this.aria2.getGlobalStat()
     this.startIntervalUpdate()
   }catch(e){
    if(e == 'WS_CONNECT_ERROR'){
               clearInterval(this.intervalId)
             }else{
               throw e
             }
   }
  },
  watch:{
   async aria2(){
      this.globalStat = await this.aria2.getGlobalStat()
      this.startIntervalUpdate()
    }// 当切换服务器后重新连接时,aria2变了，则执行startIntervalupdate
  },
  beforeDestroy(){
    clearInterval(this.intervalId)
  }
}
</script>
